html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0; }

.headerofsite {
  display: block;
  position: relative;
  width: 100%;
  height: 100vh;
  background: linear-gradient(45deg, #EECFBA, #C5DDE8); }
  .headerofsite .h2 {
    position: absolute;
    color: white;
    top: 45%;
    width: 100%; }
    .headerofsite .h2 h2 {
      color: #000; }

section {
  position: relative;
  height: 100%; }
  section h2.text-center {
    color: #000;
    border: none;
    text-shadow: none;
    margin: 50px 0 35px; }
  section div.container {
    display: block;
    position: relative; }
    section div.container div.row {
      display: block;
      position: relative; }
      section div.container div.row .row {
        flex-direction: row;
        justify-content: center !important; }
        section div.container div.row .row li {
          list-style: none;
          align-items: center;
          padding: 15px; }
          section div.container div.row .row li a {
            text-decoration: none;
            color: forestgreen; }
      section div.container div.row div.layout {
        position: absolute;
        width: 100%;
        height: 80vh;
        padding: 15px;
        overflow: hidden; }
        section div.container div.row div.layout div.free-wall {
          width: 100%;
          height: 100%; }
          section div.container div.row div.layout div.free-wall div.brick img {
            max-width: 300px; }

@keyframes start {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }
          section div.container div.row div.layout div.free-wall .free-wall .brick[data-state="init"] {
            display: none; }
          section div.container div.row div.layout div.free-wall .free-wall .brick[data-state="start"] {
            display: block;
            animation: start 0.5s; }
          section div.container div.row div.layout div.free-wall .free-wall .brick[data-state="move"] {
            transition: top 0.5s, left 0.5s, width 0.5s, height 0.5s; }

ul.controls {
  font-size: 15px;
  margin: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center; }
  ul.controls li {
    text-transform: uppercase;
    position: relative;
    list-style: none;
    padding: 10px;
    cursor: pointer;
    transition: background 150ms;
    color: #28c700; }
  ul.controls li.active {
    color: black; }

.galery_container {
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  overflow: hidden; }
  .galery_container .mix {
    display: inline-block;
    position: relative;
    cursor: pointer;
    box-sizing: content-box;
    overflow: hidden;
    margin: 12px 0; }
    .galery_container .mix img[title] h3 {
      color: red; }
